.items{
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 1.5vw;
    font-weight: 400;

    .text{
        ul{
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-right: 1.5vw;
        }

        li{
            margin-left: 1.5vw;
            margin-right: 1.5vw;
            
            a{
                font-size: inherit;
                font-weight: inherit;
                color: inherit;
                text-decoration: none;
                transition: all 500ms ease;

                &:hover{
                    color: yellow;
                    font-size: 1.7vw;
                }
            }
        }
    }

    .social{
        ul{
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-right: 1.5vw;
        }

        li{
            margin-left: 1.5vw;
            margin-right: 1.5vw;
            
            a{
                font-size: inherit;
                font-weight: inherit;
                color: inherit;
                text-decoration: none;
                transition: all 500ms ease;

                &:hover{
                    color: yellow;
                }
            }
        }
    }
}