@import "../../global.scss";

.quotes{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: $red;
  
  .slideShow{
    width: 100%;

    .item-slide{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      .container{
        position: relative;

        img{
          position: absolute;
          left: 0px;
          top: 0px;
          width: 60px;
          height: 60px;

          @include mobile{
            width: 30px;
            height: 30px;
          }
        }

        .content{
          margin: 15px;
          background-color: white;
          width: 500px;
          margin: 20px;
          padding: 20px;
          border-radius: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          color: black;

          @include mobile{
            width: 250px;
            margin: 10px;
            padding: 10px;
          }

          .quote{
            padding: 10px;
            font-size: 15px;
            font-style: italic;
            color: inherit;
            text-align: center;

            @include mobile{
              padding: 5px;
              font-size: 12px;
            }
          }

          .author{
            padding: 10px;
            font-size: 13px;
            font-weight: 900;
            color: $red;

            @include mobile{
              padding: 5px;
              font-size: 12px;
            }
          }
        }
      }
    }
  }

  .dot{
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
    margin: 5px;
    align-self: center;
    justify-content: center;

    @include mobile{
      width: 20px;
      height: 20px;
    }
  }

  .dot.active{
    width: 30px;
    height: 30px;
    background-color: yellow;
    border-radius: 50%;
    margin: 5px;
    align-self: center;
    justify-content: center;

    @include mobile{
      width: 20px;
      height: 20px;
    }
  }
}