@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;
  background-color: $red;
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;

  &.active {
    right: 0;
  }

  .text{
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 300;
        color: white;

        li {
        margin-bottom: 25px;
        a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }

        &:hover {
            font-weight: 500;
            color: yellow;
        }
        }
    }
    }

    .social{
        margin-top: 20px;
        
        ul{
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-right: 1vw;
        }

        li{
            margin-left: 1vw;
            margin-right: 1vw;
            
            a{
                font-size: inherit;
                font-weight: inherit;
                color: white;
                text-decoration: none;
                transition: all 500ms ease;

                &:hover{
                    color: yellow;
                }
            }
        }
    }
}