@import "../../global.scss";

.intro{
    display: flex;
    
    .left{
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0.5;
        overflow: hidden;
        animation: intro-left 1s ease-in;

        @keyframes intro-left {
            0%{
                transform: translateX(-1000px);
            }
            100%{
                transform: translateX(0);
            }
        }

        .imageContainer {
            width: 600px;
            height: 600px;
            background-color: $red;
            border-radius: 50%;
            margin-left: 5px;
            margin-right: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
        
            @include mobile{
                align-items: center;
                width: 500px;
                height: 500px;
            }
        
            img {
                height: 80%;
        
                @include mobile{
                    height: 70%;
                }
            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;
        animation: intro-right 1s ease-in;

        @keyframes intro-right {
            0%{
                transform: translateX(calc(100vw + 1000px));
            }
            100%{
                transform: translateX(0);
            }
        }

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
      
            @include mobile{
              padding-left: 0;
              align-items: flex-start;
            }
      
            h1 {
              font-size: 70px;
              margin: 10px 0;
              color: black;
      
              @include mobile{
                font-size: 60px;
              }
            }
            h2 {
              font-size: 30px;

              @include mobile{
                  font-size: 40px;
              }
            }
            h3 {
              font-size: 30px;
              font-weight: 900;
      
              @include mobile{
                font-size: 30px;
              }
      
              span {
                font-size: inherit;
                color: $red;
              }
      
              .ityped-cursor {
                animation: blink 1s infinite;
              }
      
              @keyframes blink {
                  50%{
                      opacity: 1;
                  }
                  100%{
                      opacity: 0;
                  }
              }
            }
          }
          a {
            position: absolute;
            bottom: 10px;
            padding-top: 10px;
            left: 40%;
            height: 40px;
            text-align: center;

            @include mobile{
                display: none;
            }
      
            img {
              width: 30px;
              animation: arrowBlink 2s infinite;
            }
      
            @keyframes arrowBlink {
                0%, 100% {
                    transform: translateY(0);
                  }
                  50% {
                    transform: translateY(-10px);
                  }
            }
        }
    }

    @include mobile{
        flex-direction: column-reverse;
        align-items: center;
    }
}