@import "../../global.scss";

.about{
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0.5;
        padding: 2vmin;
        overflow: hidden;
        
        .textContainer{
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            border-radius: 5%;
            padding: 5vmin;
            margin: 3vmin;
            background-color: $red;
            color: white;
            text-align: justify;
    
            h1{
                font-size: 60px;
                font-weight: 900;
                color: inherit;
                text-shadow: 4px 4px black ;

                @include mobile{
                    font-size: 40px;
                }
            }

            h2{
                font-size: 30px;
                font-weight: 700;
                color: inherit;

                @include mobile{
                    font-size: 20px;
                }
            }

            h3{
                font-size: 25px;
                font-weight: 300;
                color: inherit;

                @include mobile{
                    font-size: 18px;
                }
            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;
        width: 100%;

        .skills {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
      
            @include mobile{
              padding-left: 0;
              align-items: center;
            }
    
            .title{
              margin: 10px;
              color: black;
              font-size: 40px;
              font-weight: 700;
    
              @include mobile{
                font-size: 14px;
                margin: 3px;
              }
            }
      
            .item{
              width: 80%;
              margin: 10px;
              color: black;
              font-size: 20px;
              align-items: center;

              .progressBar{
                  width: 80%;
              }
    
              @include mobile{
                font-size: 8px;
                margin: 3px;
              }
            }
        }
    }
}