@import "../../global.scss";

.contact{
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0.5;

        @include mobile{
            flex: 0.3;

            img{
                height: 100%;
            }
        }
    }

    .rigth{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex: 0.5;
        position: relative;

        @include mobile{
            flex: 0.7;
        }

        h1{
            font-size: 3vmax;
            font-weight: 700;
            margin: 1.7vmax;
        }

        form{
            display: flex;
            flex-direction: column;
            font-size: 1.3vmax;
            font-weight: 500;

            label{
                font-size: inherit;
                font-weight: inherit;
            }

            input{
                width: 20vmax;
                font-size: 20px;
                border-radius: 20px;
                border-width: 5px;
                padding: 0.2vmax;
                border-color: gray;

                &:focus{
                    border-color: $red;
                }
            }

            input[type=submit]{
                align-self: center;
                width: 20vmax;
                font-size: 1.2vmax;
                font-weight: 900;
                color: white;
                border-radius: 20px;
                border-color: $red;
                padding: 0.2vmax;
                background-color: $red;

                &:hover{
                    background-color: green;
                    border-color: green;
                }
            }

            textarea{
                width: 20vmax;
                font-size: 20px;
                border-radius: 20px;
                border-width: 5px;
                padding: 0.2vmax;
                border-color: gray;
                resize: none;

                &:focus{
                    border-color: $red;
                }
            }
        }
    }
}