@import "../../global.scss";

.portfolio{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: $red;

    .title{
        font-size: 60px;
        font-weight: 900;
        color: white;
        overflow: hidden;
    }

    .slider{
        width: 80%;
        overflow: hidden;

        @include mobile{
            width: 100%;
        }

        .item-slide{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        
            .container{
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
        
                .content{
                margin: 15px;
                background-color: white;
                width: 90%;
                margin: 20px;
                border-radius: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: row;
                color: black;
        
                @include mobile{
                   flex-direction: column;
                   width: 80%;
                }

                .left{
                    flex: 0.5;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 5px;

                    img{
                        object-fit: scale-down;
                        width: 100%;
                        border-radius: 20px;
                    }
                }

                .right{
                    position: relative;
                    flex: 0.5;
                    display: flex;
                    padding: 20px;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;

                    @include mobile{
                        padding: 3px;
                    }

                    .name{
                        font-size: 30px;
                        font-weight: 700;
                        color: inherit;
                        padding: inherit;
                        margin: inherit;
                        text-align: center;
    
                        @include mobile{
                            font-size: 20px;
                            margin: 3px;
                        }
                    }
    
                    .type{
                        font-size: 16px;
                        font-weight: 900;
                        color: gray;
                        padding: 2px;
                        margin: 5px;
                        text-align: center;
    
                        @include mobile{
                            font-size: 14px;
                        }
                    }
    
                    .decription{
                        font-size: 18px;
                        font-weight: 500;
                        color: inherit;
                        padding: inherit;
                        text-align: center;
                        margin: inherit;
                        text-align: center;
    
                        @include mobile{
                            font-size: 10px;
                        }
                    }
    
                    a{
                        width: 80%;
                        text-decoration: none;
                        color: white;
                        padding: 20px;
                        font-size: 20px;
                        font-weight: 600;
                        text-align: center;
    
                        @include mobile{
                            padding: 10px;
                            font-size: 14px;
                            width: 100%;
                        }
    
                        .button{
                            background-color: black;
                            border-radius: 20px;
                            padding: 20px;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-evenly;
                            align-items: center;
    
                            @include mobile{
                                padding: 10px;
                            }
    
                            &:hover{
                                animation: button-url 2s infinite;
                                background-color: darkgoldenrod;
            
                                @keyframes button-url {
                                    0%{
                                        transform: scale(1,1);
                                    }
                                    50%{
                                        transform: scale(1.05,1.05);
                                    }
                                    100%{
                                        transform: scale(1,1);
                                    }
                                }
                            }
                        }
                    }
                }

                }
            }
        }
    }
}