@import "../../global.scss";

.experiences{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $red;
    color: black;

    .title{
        font-size: 60px;
        font-weight: 900;
        color: white;
        overflow: hidden;

        @include mobile{
            font-size: 40px;
        }
    }

    .content{
        width: 60%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        overflow: visible;
        padding: 10px;
        margin: 10px;

        @include mobile{
            width: 70%;
            padding: 3px;
            margin: 3px;
        }

        .item{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            margin: 10px;
            padding: 10px;
            background-color: rgb(255, 255, 255);
            border-radius: 30px 5px 30px 5px;
            box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
            transition: all .5s ease;

            &:hover{
                transform: scale(1.05);
            }

            .text{
                flex: 0.85;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color: inherit;
                text-align: center;

                .top{
                    flex: 0.2;
                    font-size: 26px;
                    font-weight: 700;

                    @include mobile{
                        font-size: 15px;
                    }
                }

                .bottom{
                    flex: 0.8;
                    position: relative;
                    font-size: 18px;
                    font-weight: 500;

                    @include mobile{
                        font-size: 12px;
                    }
                }

                a{
                    color: $red;

                    &:hover{
                        color: goldenrod;
                    }
                }

            }
    
            img{
                object-fit: scale-down;
                flex: 0.15;
                height: 80px;
            }
        }
    }

    .working-in-progress{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        margin: 10px;
        padding: 10px;
        overflow: hidden;
        

        .text{
            flex: 0.5;
            font-size: 25px;
            font-weight: 700;
            color: white;
            text-align: center;

            @include mobile{
                font-size: 15px;
            }
        }

        img{
            object-fit: scale-down;
            flex: 0.5;
            height: 80px;
            animation: fullRotation 5s infinite;

            @keyframes fullRotation {
                0%{
                    transform: rotate(0deg);
                }

                100%{
                    transform: rotate(360deg);
                }
            }
        }
    }
}