@import "../../global.scss";

.university {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: black;
    overflow: hidden;

        .title{
            font-size: 60px;
            font-weight: 900;
            text-shadow: 4px 4px black;
            color: $red;
            overflow: hidden;
            margin: 10px;

            @include mobile{
                font-size: 40px;
            }
        }

        .description{
            background-color: $red;
            border-radius: 20px;
            margin: 10px;
            padding: 5px;
            overflow: hidden;
            align-items: center;

            @include mobile{
                margin: 3px;
                padding: 5px;
            }

            .text{
                font-size: 15px;
                font-weight: 300;
                color: white;
                padding: 10px;
                text-align: center;

                @include mobile{
                    font-size: 12px;
                    padding: 5px;
                }
            }
        }

        .cards-container{
            width: 70%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            padding: 20px;
            overflow: hidden;

            @include mobile{
                width: 100%;
                padding: 10px;
                overflow: scroll;
            }

            .card{
                width: 100px;
                height: 100px;
                border-radius: 20px;
                margin: 10px 20px;
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
                background-color: rgb(223, 223, 223);
                position: relative;
                transition: all .5s ease;
                cursor: pointer;
                padding: 5px;
                box-shadow: 10px 10px 5px #aaaaaa;
                overflow: hidden;

            @include mobile{
                width: 60px;
                height: 60px;
            }

                .courseName{
                    position: absolute;
                    font-size: 12px;
                    font-weight: 700;
                    color: white;
                    margin: 20px;
                    display: none;

                    @include mobile{
                        font-size: 8px;
                    }
                }

                .mark{
                    font-size: 40px;
                    font-weight: 900;
                    color: $red;
                    margin: 20px;
                    z-index: 1;

                    @include mobile{
                        font-size: 17px;
                    }
                }

                &:hover{
                    background-color: $red;
                    transform: scale(1.2,1.2);
                    .mark{
                        opacity: 0;
                        z-index: 0;
                    }

                    .courseName{
                        display: block;
                    }
                }
            }
        }
}